<template>
  <v-layout row justify-space-around>
    <v-flex xs5>
      <v-card>
        <v-card-text ref="sale" style="width:100%;height:400px;">
        </v-card-text>
      </v-card>
    </v-flex>

    <v-flex xs5>
      <v-card>
        <v-card-text ref="pie" style="width:100%;height:400px;">
        </v-card-text>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
  //  ECharts 
  var echarts = require('echarts/lib/echarts');
  require('echarts/lib/chart/bar');
  require('echarts/lib/chart/pie');

  export default {
    name: "dashboard",
    data(){
      return {

      }
    },
    mounted(){
      var sale = echarts.init(this.$refs.sale);

      // 
      var option = {
        title: {
          text: ''
        },
        tooltip: {},
        legend: {
          data:['']
        },
        xAxis: {
          data: ["","","","","",""]
        },
        yAxis: {},
        series: [{
          name: '',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20]
        }]
      };

      // 
      sale.setOption(option);

      const pie = echarts.init(this.$refs.pie);

      pie.setOption({
        roseType: 'angle',
        title: {
          text: ''
        },
        series : [
          {
            name: '',
            type: 'pie',
            radius: '55%',
            data:[
              {value:235, name:''},
              {value:274, name:''},
              {value:310, name:''},
              {value:335, name:''},
              {value:400, name:''}
            ]
          }
        ],
        itemStyle: {
          emphasis: {
            // 
            shadowBlur: 200,
            // 
            shadowOffsetX: 0,
            // 
            shadowOffsetY: 0,
            // 
            shadowColor: 'rgba(0, 0, 0, 0.5)'
          }
        }
      })
    }
  }
</script>

<style scoped>

</style>



